* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.page {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
}

.page__body {
  margin: 0;
}

.nav {
  text-transform: uppercase;
  font-weight: 500;
}

.nav__list {
  list-style: none;
  display: flex;
}

.nav__list :not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: #060b35;
  padding-block: 23px;
  text-decoration: none;
  transition: all .3s;
  display: block;
  position: relative;
}

.nav__link--hover:hover, .nav__link--active {
  color: #00acdc;
}

.nav__link--active:before {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding-inline: 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo-link, .stars {
  display: flex;
}

.stars :not(:last-child) {
  margin-right: 4px;
}

.stars--active :not(:last-child) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__item {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.buy {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  padding: 12px 70px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s;
}

.buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.grid-container {
  grid-template-columns: repeat(auto-fit, minmax(200px, 200px));
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  min-height: 134px;
  margin-bottom: 40px;
  padding-inline: 3px;
}

.card__title {
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover .card__title {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__review {
  color: #060b35;
  font-size: 10px;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  line-height: 18px;
}

.card__price-number {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}
